@import '/src/styles/variables';
@import '/src/styles/mixins';

#main {
	display: flex;
	align-items: center;
	justify-content: center;

	font-family: 'Kdam Thmor Pro', sans-serif;
	color: lightgray;

	height: 100vh;
	width: 100%;
	background-color: #3a342f;
}

#score {
	width: 8rem;
	height: 85vh;
	display: flex;
	flex-direction: column;
	gap: 1rem;
	align-items: flex-end;
	margin-right: 3rem;
	font-size: 1.5rem;
}

#control {
	display: flex;
	flex-direction: column;
	margin-left: 3rem;
	height: 85vh;
}

#title {
	font-size: 3rem;
	font-weight: bold;
	text-shadow: 0 0 1rem black;
	text-align: end;
	margin: 1rem 2rem 0;
	animation: colorChange infinite 10s ease-in-out;
}

#controlContainer {
	flex-grow: 1;
	width: 25vw;
	padding: 2rem;
	background-color: #4d453f;
	border-radius: 2rem;
	box-shadow: 0 0 10vmin 0px #222;
}

#moveControl {
	display: flex;
	flex-direction: column;
	gap: 2rem;

	& > div {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 2rem;
	}
}

#status {
	margin-top: 0;
	margin-bottom: -2rem;
}

.red {
	color: $red;
}

.blue {
	color: $blue;
}

.yellow {
	color: $yellow;
}

.green {
	color: $green;
}
